/*******************************************************************************
 * Variables used throughout the theme.
 * To adjust anything, simply edit the variables below and rebuild the theme.
 ******************************************************************************/


// Colors
$red-color: #FF3636 !default;
$red-color-dark: #B71C1C !default;
$orange-color: #F29105 !default;
$blue-color: #0076df !default;
$blue-color-dark: #00369f !default;
$cyan-color: #2698BA !default;
$light-cyan-color: lighten($cyan-color, 25%);
$green-color: #00ab37 !default;
$green-color-lime: #B7D12A !default;
$green-color-dark: #056a1b !default;
$dark-green: #013b27 !default;
$lighter-dark-green: #014930 !default;
$lightest-dark-green: #013b27 !default;
$green-color-light: #ddffdd !default;
$green-color-bright: #11D68B !default;
$purple-color: #B509AC !default;
$light-purple-color: lighten($purple-color, 35%);
$pink-color: #f92080 !default;
$pink-color-light: #ffdddd !default;
$yellow-color: #efcc00 !default;
$lavender: #f9e7ff !default;
$dark-lavender: #f7c3fa !default;
$light-lavender: #f9f1fb !default;
$dark-purple: #21012b !default;
$not-so-dark-purple: #410354 !default;

$grey-color: #828282 !default;
$grey-color-light: lighten($grey-color, 40%);
$grey-color-dark: #1C1C1D;
$grey-900: #212529;

$white-color: #ffffff !default;
$black-color: #000000 !default;


// Theme colors

$code-bg-color-light: rgba($purple-color, 0.05);
$code-bg-color-dark: #2c3237 !default;

// Theme fonts
$font-name: 'Inter';
$title-font-name: 'InstrumentSerif';

// Sizes
$size: 16px;
$size-l: $size * 1.5;
$size-xl: $size * 2.0;
$size-xxl: $size * 2.5;
$size-m: $size * 1.25;
$size-s: $size * 0.75;
$size-xs: $size * 0.5;
$size-xxs: $size * 0.25;

$font-size-huge: 3.0em;
$font-size-larger: 2.0em;
$font-size-large: 1.2em;
$font-size: 1.0em;
$font-size-small: 0.9em;
$font-size-smaller: 0.8em;
$font-size-tiny: 0.7em;

$border-size-light: 1px;
$border-size: 2.0px;
$border-size-heavy: 4px;