/*******************************************************************************
 * Themes
 ******************************************************************************/

// Define Sass variables for light theme
$light-global-bg-color: $lavender;
$light-global-code-bg-color: $code-bg-color-light;
$light-global-text-color: $black-color;
$light-global-text-color-light: $grey-color;
$light-global-theme-color: $green-color-dark;
$light-global-hover-color: $green-color-dark;
$light-global-footer-bg-color: $light-global-bg-color;
$light-global-footer-text-color: $grey-color-light;
$light-global-footer-link-color: $white-color;
$light-global-distill-app-color: $grey-color;
$light-global-divider-color: rgba(0,0,0,.2);
$light-global-card-bg-color: $light-lavender;

// Define Sass variables for dark theme
$dark-global-bg-color: $dark-green;
$dark-global-code-bg-color: $code-bg-color-dark;
$dark-global-text-color: $grey-color-light;
$dark-global-text-color-light: $grey-color-light;
$dark-global-theme-color: $dark-lavender;
$dark-global-hover-color: $dark-lavender;
$dark-global-footer-bg-color: $grey-color-light;
$dark-global-footer-text-color: $grey-color-dark;
$dark-global-footer-link-color: $black-color;
$dark-global-distill-app-color: $grey-color-light;
$dark-global-divider-color: rgba(255,255,255, 0.5);
$dark-global-card-bg-color: $lighter-dark-green;

// Size variables
$size: 0.5em;
$size-l: $size * 1.5;
$size-xl: $size * 2.0;
$size-xxl: $size * 2.5;
$size-s: $size * 0.75;
$size-xs: $size * 0.5;
$size-xxs: $size * 0.25;
$font-size: 1.2em;

:root {
 --global-bg-color: #{$light-global-bg-color};
 --global-code-bg-color: #{$light-global-code-bg-color};
 --global-text-color: #{$light-global-text-color};
 --global-text-color-light: #{$light-global-text-color-light};
 --global-theme-color: #{$light-global-theme-color};
 --global-hover-color: #{$light-global-hover-color};
 --global-footer-bg-color: #{$light-global-footer-bg-color};
 --global-footer-text-color: #{$light-global-footer-text-color};
 --global-footer-link-color: #{$light-global-footer-link-color};
 --global-distill-app-color: #{$light-global-distill-app-color};
 --global-divider-color: #{$light-global-divider-color};
 --global-card-bg-color: #{$light-global-card-bg-color};

 
 .fa-sun {
 display: none;
 }
 .fa-moon {
 padding-left: 10px;
 padding-top: 12px;
 display: block;
 }

 .repo-img-light {
 display: block;
 }
 .repo-img-dark {
 display: none;
 }
}


// Dark theme styles
html[data-theme='dark'] {
 --global-bg-color: #{$dark-global-bg-color};
 --global-code-bg-color: #{$dark-global-code-bg-color};
 --global-text-color: #{$dark-global-text-color};
 --global-text-color-light: #{$dark-global-text-color-light};
 --global-theme-color: #{$dark-global-theme-color};
 --global-hover-color: #{$dark-global-hover-color};
 --global-footer-bg-color: #{$dark-global-footer-bg-color};
 --global-footer-text-color: #{$dark-global-footer-text-color};
 --global-footer-link-color: #{$dark-global-footer-link-color};
 --global-distill-app-color: #{$dark-global-distill-app-color};
 --global-divider-color: #{$dark-global-divider-color};
 --global-card-bg-color: #{$dark-global-card-bg-color};

 .fa-sun {
 padding-left: 10px;
 padding-top: 12px;
 display: block;
 }
 .fa-moon {
 display: none;
 }

 .repo-img-light {
 display: none;
 }
 .repo-img-dark {
 display: block;
 }
}
